import React, { useRef, useEffect } from 'react';
import * as d3 from 'd3';

import NextLink from '../src/Link';
import MainTemplate from '../src/MainTemplate';

import drawTheTree from '../src/dndtree';
import flare from '../src/flare.json';

export default function VisTest1() {
  let divref = useRef(null);
  useEffect(() => {
    const div = divref.current;
    const treeContainer = document.createElement("div");
    div.appendChild(treeContainer);
    drawTheTree(treeContainer, flare);

    return () => {
      div.removeChild(treeContainer);
    }
  }, [divref.current]);

  return (
    <MainTemplate>
      <Typography variant="body1">
        Source: <NextLink href="https://observablehq.com/@d3/tidy-tree">@d3/tidy-tree</NextLink>
      </Typography>
      <div ref={divref} />
    </MainTemplate>
  );
}
